@import url(https://fonts.googleapis.com/css?family=Noto+Sans&display=swap);
div {
    top: 5px;
    position: relative;
}

div > span {
    position: absolute;
    top: 10px;
    left: 15px;
}

div > input[type=text] {
    width: 300px;
    height: 2.5em;
    padding-left: 40px;
    border-radius: 25px;
    text-indent: 10px;  
    border: 0px;
}

div > input[type=text]:focus{
    outline: none;
}

/***** Light Theme *****/
.search-container > span {
    color: #656C75;
}

.search-container > input[type=text] {
    background-color: #F0F2F5;
    color: #656C75;
}

.search-container > input[type=text]::-webkit-input-placeholder{
    color: #656C75;
}

.search-container > input[type=text]:-ms-input-placeholder{
    color: #656C75;
}

.search-container > input[type=text]::-ms-input-placeholder{
    color: #656C75;
}

.search-container > input[type=text]::placeholder{
    color: #656C75;
}

/***** Dark Theme *****/
.search-container-dark > span {
    color: #ACAFB4;
}

.search-container-dark > input[type=text] {
    background-color: #3A3B3C;
    color: #ACAFB4;
}

.search-container-dark > input[type=text]::-webkit-input-placeholder  {
    color: #ACAFB4;
}

.search-container-dark > input[type=text]:-ms-input-placeholder  {
    color: #ACAFB4;
}

.search-container-dark > input[type=text]::-ms-input-placeholder  {
    color: #ACAFB4;
}

.search-container-dark > input[type=text]::placeholder  {
    color: #ACAFB4;
}

@media screen and (max-width: 600px) {
    div > input[type=text] {
        width: 80%;
    }

    div > input[type=text]::-webkit-input-placeholder {
        font-size: 12px;
    }

    div > input[type=text]:-ms-input-placeholder {
        font-size: 12px;
    }

    div > input[type=text]::-ms-input-placeholder {
        font-size: 12px;
    }

    div > input[type=text]::placeholder {
        font-size: 12px;
    }   
}
header {
    display: flex;
    width: 100%;
    background-color:white;
    height: 44px;
    z-index: 400;
    position: fixed;
    top: 0;
    padding: 15px;  
}

/***** Light Theme *****/
.header-light {
    background-color: white;
    border-bottom: solid 1px #F0F2F5;
}

/***** Dark Theme *****/
.header-dark {
    background-color: #242526;
    border-bottom: solid 1px #3A3B3C;
}

header > label {
    align-self: center;
    margin-left: auto;
    margin-right: 50px;
}

.searchBar {
    flex: 1 1;
}

.sidebar {
    display: flex;
    margin-right: 1%;
}

.sidebar-light {
    background-color: white;
}

.sidebar-dark {
    background-color: #242526;
}

.topbar-items {
    display: flex;
    justify-content: space-around;
    align-items: center;
    list-style-type: none; 
    margin: 0;
    width: 100%;
    height: 100%;
}

.navbar-li {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: flex-start;
}

.navbar-li a {
    text-decoration: none;
}

.nav-visibility {
    visibility: hidden;
}

.nav-light {
    color: black;
}

.nav-dark {
    color: white;
}

.nav-text {
    text-decoration: none;
    font-family: sans-serif;
    font-size: 18px;
}

.nav-selected-light {
    color: #34a1cd ;
}

.nav-selected-dark {
    color: #ade6e6 ;
}

input[type=checkbox] {
    -webkit-appearance: none;
    visibility: hidden;
    display: none;
}

.check {
    position: relative;
    display: block;
    width: 40px;
    height: 20px;
    background: #092c3e;
    cursor: pointer;
    border-radius: 20px;
    border: solid black 1px;
    overflow: hidden;
    transition: ease-in 0.5s;
}

input[type=checkbox]:checked ~ .check { 
    background: white;
}

.check::before {
    content: '';
    position: absolute;
    top: 2px;
    left: 2px;
    background: white;
    width: 16px;
    height: 16px;
    border-radius: 50%;
    transition: 0.5s;
}

input[type=checkbox]:checked ~ .check::before {
    transform: translateX(20px);
    background: #092c3e;
}

.navbar {
    visibility: hidden;
}

.menu-bar {
   visibility: hidden;
}

@media screen and (max-width: 600px) {
    .nav-visibility {
        visibility: visible;
    }

    .navbar {
        visibility: visible;
        background-color: transparent;
        display: flex;
        justify-content: flex-end;
        align-items: center;
    }
    
    .menu-bar {
        visibility: visible;
        margin-right: 2rem;
        font-size: 2rem;
        background: none;
    }

    .topbar-items {
        padding-top: 5em;
        flex-direction: column;
        justify-content: flex-start;
    }

    .sidebar {
        width: 250px;
        height: 100vh;
        top: 0;
        right: -100%;
        margin-right: 0;
        justify-content: center;
        position: fixed;
        transition: 800ms;
    }
    
    .sidebar.sidebar-active {
        right: 0;
        transition: 350ms;
    }
    
    .navbar-li {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        padding: 8px 0px 8px 16px;
        list-style: none;
        height: 60px;
    }

    .navbar-li a {
        text-decoration: none;
        display: flex;
    }
    
    .nav-text {
        font-family: sans-serif;
        font-size: 15px;
        padding-left: 15px;
    }

    .nav-icon {
        display: flex;
    }
    
    .check {
        left: 20px;
    }

    .navbar-toggle {
        visibility: visible;
        background-color: transparent;
        width: 100%;
        height: 80px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
    }
}
li {
    display: flex;
    justify-content: center;
    align-items: center;
}

.img-container {
    position: relative;
    top: 0;
}

button {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute; 
    width: 30px;
    height: 30px;
    right: 10px;
    z-index: 5;
    border-radius: 5px;
    outline: none;
}

.favourite-button {
    top: 10px;
}

.download-button {
    bottom: 10px;
}

.img-card {
    max-width: 100%;
    max-height: 100%;
    width: auto;
    height: auto;
    border-radius: 15px;
    position: relative;
    z-index: 10;
    transition: all 500ms;
}

.img-card:hover {
    transform: translateY(-3px) scale(1.1);   
    z-index: 0;
}

button:hover {
    cursor: pointer;
}

button:hover ~ .img-card {
    transform: translateY(-3px) scale(1.1);   
    z-index: 0;
}

.cover {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute; 
    width: 30px;
    height: 30px;
    border-radius: 5px;
}

.download-icon {
    z-index: 4;
}

.favourite-icon {
    z-index: 4;
}

.white-cover {
    background-color: #F0F2F5;
    z-index: 5;
}

.black-cover {
    background-color: black;
    z-index: 5;
}

@media screen and (max-width: 600px) {
    button {
        width: 20px;
        height: 20px;
    }

    .cover {
        width: 20px;
        height: 20px;
    }

    .favourite-icon {
        width: 10px;
        height: 10px;
    }

    .download-icon {
        width: 10px;
        height: 10px;
    }
}

.image-gallery {
    padding-top: 100px;  
    padding-bottom: 50px;
}
   
.image-gallery-ul {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    grid-gap: 40px 60px; 
    list-style-type: none;  
    margin: 0;
    padding-left: 30px;
    padding-right: 30px;
}

@media screen and (max-width: 600px) {
    .image-gallery-ul {
        grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
        grid-gap: 20px 40px;    
    }
}

.fav-gallery {
    padding-top: 100px;  
    padding-bottom: 50px;
}
   
.fav-gallery-ul {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    grid-gap: 40px 60px; 
    list-style-type: none;  
    margin: 0;
    padding-left: 30px;
    padding-right: 30px;
}

@media screen and (max-width: 600px) {
    .fav-gallery-ul {
        grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
        grid-gap: 20px 40px;
    }
}

