    width: 100%;
    position: absolute;
    top: 50%;
    transform: translateY(50%);
    text-align: center;
}

.light-mode {
    color: black;
}

.dark-mode {
    color: white;
}

h1 {
    font-size: 150px;
    margin: 0;
